
import { getTodaysTime } from '../../utils';
import { formatTodaysTime, formatDiscount, elipseFormat } from '../PlaceDetails/PlaceDetails';

const formatDealCount = (deals) => {
  const ndeals = deals.length; 
  const nfood = deals.filter(deal => deal.type === 'food').length;
  const ndrink = deals.filter(deal => deal.type === 'drink').length;
  const deal_word = `deal${ndeals > 1 ? 's' : ''}`
  if (nfood > 0 && ndrink > 0) {
    return `${ndeals} ${deal_word} (${nfood} food, ${ndrink} drink)`
  }
  if (nfood > 0) {
    return `${nfood} food ${deal_word}`
  }
  return `${ndrink} drink ${deal_word}`
}


const mkPhotoUrl = (photoRef) => {
  return `https://maps.googleapis.com/maps/api/place/photo?photo_reference=${photoRef}&key=AIzaSyCxfXAPgVM8ownCZdr8dFbQSg76chB7P4s&maxheight=350`;
};


const Mobile = ({ places, timeFilter }) => (
  <>
    <div class="appbar">
      <div class="searchbar">
        <div class="text">
          Search Place or Deal...
        </div>
      </div>
      <div class="filterbar">
        <button class="filter text">Sort By ▼</button>
        <button class="filter text">Now ▼</button>
        <button class="filter text">Deals ▼</button>
      </div>
    </div>
    <div class="map">
      <img alt="map" src="https://media.wired.com/photos/59269cd37034dc5f91bec0f1/master/pass/GoogleMapTA.jpg" />
    </div>
    <div class="places-list">
      <div class="place-holder"></div>
      <div class="place-holder-seperator">
        <div class="tiny-gray-box"></div>
      </div>
      {places.map(place => (
        <div class="place">
          <div class="image-horizontal-scroll">
            {place.photos.map(mkPhotoUrl).map(url => (
              <img alt="" src={url} />
            ))}
          </div>
          <div class="place-details">
            <div class="place-name">{place.place_name}</div>
            <div>⭐ {place.rating} ({place.user_ratings_total})</div>
            <div>
              {formatDealCount(place.deals)} · {'$'.repeat(place.price_level)} ·
              4.7 mi
            </div>
            <div>{formatTodaysTime(getTodaysTime(place.times), timeFilter)}</div>
          </div>
          <div class="deals-preview-container">
            <div class="deals-preview">
              {place.deals.slice(0, 5).map((deal) => (
                formatDiscount(deal.discount_amount, deal.discount_marker) + ' ' + elipseFormat(35, deal.item)
              )).join(' · ')}
            </div>
          </div>
        </div>
      ))}
    </div>
  </>
);

export default Mobile;